import React from 'react'
import Header from 'components/Header.jsx'
import './style.styl'

export class App extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      title: <h1>App</h1>
    }
  }

  render () {
    return (
      <div>
        <Header name="React"/>
        {this.state.title}
      </div>
    )
  }
}



